<template>
    <div>
        <canvas id="canvas"><span>浏览器不支持</span></canvas>
        <div id="myCarousel" class="carousel slide">
            <!-- 轮播（Carousel）指标 -->
            <ol class="carousel-indicators">
                <li data-target="#myCarousel" data-slide-to="0" class="active"></li>
                <li data-target="#myCarousel" data-slide-to="1"></li>
                <li data-target="#myCarousel" data-slide-to="2"></li>
            </ol>   
            <!-- 轮播（Carousel）项目 -->
            <div class="carousel-inner">
                <div class="item active">
                    <img src="../assets/carousel.jpg" alt="First slide">
                </div>
                <div class="item">
                    <img src="../assets/banner2.png" alt="Second slide">
                </div>
                <div class="item">
                    <img src="../assets/banner3.png" alt="Third slide">
                </div>
            </div>
            <!-- 轮播（Carousel）导航 -->
            <a class="left carousel-control" href="#myCarousel" role="button" data-slide="prev">
                <span class="glyphicon glyphicon-chevron-left" aria-hidden="true"></span>
                <span class="sr-only">Previous</span>
            </a>
            <a class="right carousel-control" href="#myCarousel" role="button" data-slide="next">
                <span class="glyphicon glyphicon-chevron-right" aria-hidden="true"></span>
                <span class="sr-only">Next</span>
            </a>
        </div> 
    </div>
</template>

<script>
export default {
  name: "Home",
  data(){
      return{
          imgSrc:['../assets/carousel.jpg','../assets/carousel.jpg','../assets/carousel.jpg']
      }
  },
  mounted(){
    const wpx = document.documentElement.clientWidth
    const hpx = document.documentElement.clientHeight
    const myCarousel = document.querySelector('#myCarousel')
    myCarousel.style.height = (hpx-50)+'px' 
    console.log(navbar)
    $('.carousel').carousel()
    $(function () {
    // 获取手指在轮播图元素上的一个滑动方向（左右）

    // 获取界面上轮播图容器
    var $carousels = $('.carousel');
    var startX,endX;
    // 在滑动的一定范围内，才切换图片
    var offset = 50;
    // 注册滑动事件
    $carousels.on('touchstart',function (e) {
        // 手指触摸开始时记录一下手指所在的坐标x
        startX = e.originalEvent.touches[0].clientX;

    });
    $carousels.on('touchmove',function (e) {
        // 目的是：记录手指离开屏幕一瞬间的位置 ，用move事件重复赋值
        endX = e.originalEvent.touches[0].clientX;
    });
    $carousels.on('touchend',function (e) {
        //console.log(endX);
        //结束触摸一瞬间记录手指最后所在坐标x的位置 endX
        //比较endX与startX的大小，并获取每次运动的距离，当距离大于一定值时认为是有方向的变化
        var distance = Math.abs(startX - endX);
        if (distance > offset){
            //说明有方向的变化
            //根据获得的方向 判断是上一张还是下一张出现
            $(this).carousel(startX >endX ? 'next':'prev');
        }
    })
}); 

        //初始化画布高宽
        let clientW = document.documentElement.clientWidth
        let clientH = document.documentElement.clientHeight
        let canvas = document.getElementById('canvas')
        canvas.width = clientW
        canvas.height = clientH
        let ctx = null
        if (canvas.getContext) {
            ctx = canvas.getContext('2d')
            console.log('支持')
        }else{
            console.log('不支持')
        }
        
        //视口变化时改变画布高宽
        window.onresize = function(){
            clientW = document.documentElement.clientWidth
            clientH = document.documentElement.clientHeight
            canvas.width = clientW
            canvas.heigth = clientH
        }
        //封装区间内随机数函数  [max,min]
        const random = (max,min)=>{
            return Math.random()*(max-min)+min
        }
        console.log(random(5,10))

        //创建Drop类
        class Drop{
            // 初始化函数
            init(){
                //产生起点X坐标
                this.x = random(0,clientW)
                //产生起点Y坐标
                this.y = 0
                //下路速度V
                this.v = random(5,6)
                //地板高度
                this.endY = random(clientH*0.7,clientH*0.8)
                //低落地板后圆r
                this.r = 1
                //rgba a透明度
                this.a = 1
                //圆变大间隔
                this.vr = 2
            }
            draw(){
                //画矩形
                console.log(this.endY)
                if(this.y<this.endY){
                    ctx.save()
                    ctx.fillStyle='white'
                    ctx.beginPath()
                    ctx.rect(this.x, this.y, 2, 20)
                    ctx.fill()
                    ctx.restore()   
                }else{//画矩形
                    ctx.save()
                    ctx.strokeStyle=`rgba(255,255,255,${this.a})`
                    ctx.beginPath()
                    ctx.arc(this.x, this.y, this.r,0, Math.PI * 2, true);
                    ctx.stroke()
                    ctx.restore()
                    
                }  
                this.move()                
            }
            //移动
            move(){
                if(this.y<this.endY){
                    this.y+=this.v
                }else{
                    if(this.a>0.02){
                        this.r+=1
                        if(this.r>40){
                            this.a*=0.9
                        }
                    }else{
                        this.init()
                    }               

                }
           
                
            }

        }
        //容器
        let dropContainer = []
        //数量
        let num = 2
        //产生多少个drop
        prodDrop(num)
        function prodDrop(num){
            for (let i = 0; i < num; i++) {
                setTimeout(()=>{
                    var drop = new Drop()
                    drop.init()
                    dropContainer.push(drop)
                },400*i)            
            }
        }


        dropMove()
        function dropMove(){
            ctx.fillStyle = 'rgba(0,0,0,0.1)'
            ctx.fillRect(0,0,clientW,clientH)            
            for (let i = 0; i < dropContainer.length; i++) {
                dropContainer[i].draw()                
            }
            requestAnimationFrame(dropMove)
        }
  }
};
</script>

<style scoped>
    #myCarousel{
        background: black;
        z-index: 5;
        opacity: 0.8;
    }
    #canvas{position: absolute;top: 0;left: 0;bottom: 0;background: black;z-index: 4;}

</style>


